
.experience{
    background-color: rgb(10, 10, 10);
    color: rgb(241, 241, 241);
    padding-top: 10%;
    text-align: center;
    font-size: 2rem;
    font-weight: 300;
    height: fit-content;
    padding-bottom: 2%;

    
}
.frontend{
    font-weight: 400;
    margin-bottom: 3%;
}
.backend{
    padding-top: 5%;
    padding-bottom: 3%;
    font-weight: 400;
}
.exp-section, .exp-section-two{


width: 70%;
min-width: 100%;
margin-left: auto;
margin-right: auto;
display: flex;
flex-wrap: wrap;
justify-content: space-around;




}

.exp{
margin-bottom: 5%;
flex-wrap: wrap;
min-width: 20%;

}
.exp:hover{
    margin-bottom: 0%;
    font-size: 2.5rem;
    transition: .5s;
}

.content{
    margin-top: 2%;
}


/* @Media ----------------------------------------- */

@media(min-width: 200px) and (max-width: 560px){
    h1{
        padding-bottom: 2%;
    }
    .exp{
        margin-top: 5%;
        /* margin-bottom: 5%; */
        
    }
    .exp:hover{
        font-size: 2rem;
        margin-bottom: 5%;
    }
    .backend{
        padding-bottom:5%;
        /* margin: 0%; */
    }
    .experience{
        height: auto;
    }
    .exp-section, .exp-section-two{
        display: block;
    }


}


.react-icon {

    
    -webkit-animation:spin 4s linear infinite;
    -moz-animation:spin 4s linear infinite;
    animation:spin 4s linear infinite;
}

.icons{
    display: inline;
    padding-left: 1%;
}


@keyframes bounce-2 {
    0%   { transform: translateY(0); }
    50%  { transform: translateY(-50px); }
    100% { transform: translateY(0); }
}
@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

