.home{
   
    background-color: rgb(241, 241, 241);
   background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("../Photos/Computer-Laptop-background.jpg");
   background-repeat: no-repeat;
   background-size: cover;
   align-items:flex-start;
   justify-content: center;
   display: flex;
   height: fit-content;
   min-height: 100vh;
   width:100%;
   margin-left: auto;
   margin-right: auto;
   box-sizing: border-box;
   font-size: 2rem;
   font-weight: 400;

   color: rgb(241, 241, 241);
   line-height: 1.3;
  
}
h1{
    font-weight: 400;
}
.header{
    margin-top: 15%;

    font-size: 2rem;
    font-family: Roboto, sans-serif;
    line-height: 1.5;
    display: flex;
    text-align:start;
    color: rgb(241, 241, 241);
   
    display: inline-block;
    
}
.button{
    color: rgb(241, 241, 241);
    text-decoration: none;
    border: rgb(241, 241, 241) solid 2px;
    padding: 1%;
    box-sizing:border-box;
    
}
.button:hover{
    padding:0;
    padding-left:1.5%;
    
    border: none;
    border-bottom: rgb(241, 241, 241) solid 2px;
  
    /* background-color: #040616; */
    /* background-color: #1a83ff; */

 
    
}

.textbox{
    width: 30%;
    margin-top: 15%;
    margin-left: 20%;
}



.section-two{
    background-color: #0f0f0f;
    color: rgb(241, 241, 241);
    display: block;
    height: 100vh;
    text-align: center;
    padding-top: 5%;
    font-size: 2rem;
}


@media(min-width: 200px) and (max-width: 650px){

  .navbar{
      position:static;
  }

  .home{
    
    font-size: 1.5rem;
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
    /* padding-left: 10%; */
    /* display: inline-block; */
    display: block;
    text-align: center;
   
  }
  .header{
      line-height: 1.3;
      text-align: center;
  }
  .textbox{
      width: 75%;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 10%;
  }
  .button{
      display: none;
  }
}
@media(min-width: 651px) and (max-width: 1023px){

    .header{
        width: 60%;
        line-height: 1.4;
        padding-left: 5%;
        padding-top: 5%;
        
       
    }
 
    .textbox{
        padding-top: 5%;
        width: 80%
    }
}